<template>
  <div>
    <h3>Profile create</h3>
    <br>
    <br>
    <div class="container">
      <div class="row">
        Username : <input type="text" v-model="username">
        <br>
        Name: <input type="text" v-model="name">
        <br>
        Date of birth : <input type="date" v-model="dob">
        <br>
        Dont show birthday year : <input type="checkbox" v-model="hide_dob">
        <br>
        <br>
        <button @click="save()" :disabled="loading">Save</button>
      </div>
      <div class="row">
        {{ errors }}
      </div>
    </div>
  </div>
</template>

<script>

export default {
  data() {
    return {
      username: '',
      dob: '',
      name: '',
      hide_dob: true,
      loading: false,
      errors: {}
    }
  },
  methods: {
    save() {
      this.loading = true
      this.$store.dispatch('auth/updateUser', {
        username: this.username,
        dob: this.dob,
        name: this.name,
        hide_dob: this.hide_dob
      })
        .then(()=> {
          this.$router.push({name: 'Home'});
        })
        .catch(err=> {
          this.errors = err.response.data
        })
        .then(()=> {
          this.loading = false;
        })
    }
  }
}
</script>

